@media screen and (min-width: 701px) {
  .title-contacts {
    border-left: 1px solid lightgray;
    padding-left: 10px;
  }

  .description__item {
    display: grid;
    grid-template-columns: 250px 1fr;
    grid-column-gap: 20px;
  }

  .history__list {
    text-align: center;
  }

  .history__company-name {
    border: 1px solid midnightblue;
    border-radius: 5px;
    padding: 15px;
    margin-bottom: 0.5em;
  }
}
