@media screen and (max-width: 700px), print {
  header,
  main {
    width: 100%;
    min-width: 300px;
  }

  .body__header {
    flex-direction: column;
  }

  .description__columns {
    display: block;
  }

  .history__list {
    display: flex;
    align-items: center;
    margin-top: 1.3em;
  }

  .history__company-name {
    border-right: 1px solid midnightblue;
    width: -moz-fit-content;
    margin: 0;
    padding-right: 10px;
  }

  .history__dates {
    padding-left: 10px;
  }

  .history__title {
    margin-top: 10px;
  }
}
